@import '../../../style/variables.scss';

.single-page {
    margin-top: 50px;
    display: grid;
    grid-template-columns: 293px 550px auto;
    column-gap: 50px;
    align-items: start;
    &__img {
        width: 293px;
        height: 450px;
        &_height {
            height: 293px;
        }
    }
    &__name {
        font-weight: bold;
        font-size: 22px;
        line-height: 29px;
    }
    &__descr {
        font-size: 18px;
        line-height: 24px;
        margin-top: 25px;
    }
    &__back {
        justify-self: end;
        font-weight: bold;
        font-size: 18px;
        line-height: 24px;
        cursor: pointer;
        border:none;
        background-color: #FFFFFF;
        &:hover {
            color: $main-color;
        }
    }
    &__price {
        margin-top: 25px;
        font-weight: bold;
        font-size: 24px;
        color: #9F0013;
    }
}